export default "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'demo-timepicker-dynamic',\r\n  templateUrl: './dynamic.html'\r\n})\r\nexport class DemoTimepickerDynamicComponent {\r\n  mytime: Date | undefined = new Date();\r\n  isValid: boolean;\r\n\r\n  update(): void {\r\n    const time = new Date();\r\n    time.setHours(14);\r\n    time.setMinutes(0);\r\n\r\n    this.mytime = time;\r\n  }\r\n\r\n  changed(): void {\r\n    console.log(`Time changed to: ${this.mytime}`);\r\n  }\r\n\r\n  clear(): void {\r\n    this.mytime = void 0;\r\n  }\r\n}\r\n";